import './App.css';
import styled from 'styled-components'
import { createContext, useState } from 'react';
import LoginCard from './components/accounts/LoginCard';
import RegisterCard from './components/accounts/RegisterCard';
import SidebarCard from './components/sidebar/SidebarCard';
import EmergencyStep from './components/step/EmergencyStep';
import wifiIcon from './images/device_wifi.png';
import signalIcon from './images/device_signal.png';
import batteryIcon from './images/device_bettery.png';
import Clock from './components/UI/Clock';
import AccountHandler from './components/accounts/AccountHandler';
import MobileContent from './components/UI/MobileContent';
// import {socket} from './APIs/Websocket'
const AuthContext = createContext();

const MobileFrame = styled.div`
  width: 100%;
  height: 595px;
  max-width: 330px; /*     */
  max-height: 1040px;
  margin: 0 auto; /*   */
  margin-top: 4%;
  border: 1px solid #ccc; /*     */
  border-radius: 10px; /*     */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2); /*   */
  background-color: #fff; /*  */
  padding: 8px; /*   */
  position: relative;
`;

const MobileHeader = styled.div`
  width: 100%;
  height: 4%;
  box-sizing: border-box;
  background-color: rgba(222, 0, 0, 0);
  padding: 6%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 4;
  img{
    display: block;
    height: 15.6px;
    width: 15.6px;
    background-size: cover;
    margin-left: 4px;
  }
`;


function App() {
  const [authState, setAuthState] = useState({
    isLoggedIn: false,
    userName: '',
    dis_level : undefined,
    nickname: undefined,
    password : undefined,
    updated_at: undefined, 
  });
  
  return (
    <MobileFrame>
      <MobileHeader>
        <Clock
          size = '1px'
        ></Clock>
        <img src={signalIcon} alt=" "/>
        <img src={wifiIcon} alt=" "/>
        <img src={batteryIcon} alt=" "/>
      </MobileHeader>
      <AuthContext.Provider value={{ authState, setAuthState }}>

        <MobileContent></MobileContent>
      </AuthContext.Provider>
    </MobileFrame>
  );
}

export default App;
export {AuthContext};

