
import React, { useState, useContext } from 'react';
import Modal from 'react-modal';

import styled from 'styled-components';
import colors from '../../Common/Color';
import backarrow from "../../images/left-arrow.png";
import redWarning from "../../images/red-warning.png";
import warning from "../../images/warning.png";
import safe from "../../images/check.png";
import { AuthContext } from '../../App';
import '../../css/modal.css';
import { updateUserAuth } from '../../APIs/Auth';

const Container = styled.div`
    padding-top: 7%;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
`;

const Topbar = styled.div`
    width: 100%;
    padding-top: 3%;
`;

const BackArrow = styled.button`
    background-color: transparent;
    border: none;
    cursor: pointer;
    width: 20px;
    height: 20px;
`;

const Image = styled.img`
    width: 20px;
    height: 20px;
`;

const BigTitle = styled.h1`
    color: ${colors.mainBlue}; 
    font-size: 180%; 
    text-align: right;
    margin: 0;
`;

const SmallTitle = styled.h3`
    color: ${colors.subGray}; 
    font-size: 85%; 
    text-align: right;
    margin: 2% 0;
`;

const Line = styled.div`
    width: 100%;
    height: 1px;
    background-color: ${colors.gray};
    content: "1";
    margin-top: 3%;
`;

const Buttons = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    cursor: pointer;
`;

const Button = styled.button`
    background-color: transparent;
    color: ${colors.black};
    border: none;
    width: 150px;
    height: 150px;
    cursor: pointer;
    font-size: 17px;
    font-weight: 700;
    text-shadow: 2px 4px 2px ${colors.gray};
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    &:hover {
        transform: scale(1.3);
    }
    img{
        width: 65%;
        height: 60%;
        margin-bottom: 5%;
        filter: drop-shadow(4px 4px 2px ${colors.gray});
    }
`;

const TitleSpan = styled.span`
    color: ${colors.red};
    font-size: 18px;
    font-weight: 700;
`;

const Span = styled.span`
    color: ${colors.mainBlue};
    font-size: 17px;
    font-weight: 700;
`;

const AlarmSpan = styled.span`
    font-size: 17px;
    font-weight: 700;
`;

const SelectButton = styled.div`
    width: 100%;
    margin-top: 5%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
`

const YesButton = styled.button`
    background-color: ${colors.mainBlue};
    color: ${colors.white};
    border: none;
    border-radius: 20px;
    cursor: pointer;
    font-size: 17px;
    font-weight: 700;
    width: 35%;
    &:hover {
        background-color: ${colors.hoverBlue};
    }
`;

const NoButton = styled.button`
    background-color: ${colors.gray};
    color: ${colors.white};
    border: none;
    border-radius: 20px;
    margin-left: 5%;
    cursor: pointer;
    font-size: 17px;
    font-weight: 700;
    width: 35%;
    &:hover {
        background-color: ${colors.hoverGray};
    }
`;

Modal.defaultStyles = {};

function EmergencyStep(props) {
    const moveUserInfo = props.moveUserInfo
    /**   */
    const {authState, setAuthState} = useContext(AuthContext);

    console.log(authState)
    const [modal1IsOpen, setModal1IsOpen] = useState(false);
    const [modal2IsOpen, setModal2IsOpen] = useState(false);
    const [modal3IsOpen, setModal3IsOpen] = useState(false);
    /**
     *    level Put  
     * @param {*} level   popup 
     */
    const handleSubmit = async (level) =>{
        console.log('?')
        //Update API 
        await updateUserAuth({
            'email': authState.userName,
            'dis_level': level,
            'password': authState.password,
            'nickname' : authState.nickname
        })
        //Context 
        setAuthState({
            isLoggedIn: true,
            userName: authState.userName,
            dis_level: level,
            nickname: authState.nickname,
            password: authState.password,
          })
        moveUserInfo()
    }
    return (
        <Container>
            <Topbar>
                <BackArrow onClick={moveUserInfo}>
                    <Image src={backarrow} alt=" "></Image>
                </BackArrow>
            </Topbar>
            <BigTitle>  </BigTitle>
            <SmallTitle>
                     
                <br />
                    !
            </SmallTitle>
            <Line></Line>
            <Buttons>
                <Button onClick={() => setModal1IsOpen(true)}>
                    <img src={redWarning} alt="  " />
                      
                </Button>
                <Modal
                    isOpen={modal1IsOpen} 
                    onRequestClose={() => setModal1IsOpen(false)}
                >
                    <TitleSpan>  </TitleSpan>
                        <p>
                                   
                            <br/>
                            60dB  
                            <br/>
                             
                        </p>
                        <Span>   </Span>
                        <p>, , ,  6.0  </p>
                        <Line></Line>
                        <br />
                        <AlarmSpan> ?</AlarmSpan>
                        <SelectButton>
                            <YesButton onClick={()=>{
                                handleSubmit(3)
                                }
                            }>
                                </YesButton>
                            <NoButton></NoButton>
                        </SelectButton>
                </Modal>
                    
                <Button onClick={() => setModal2IsOpen(true)}>
                    <img src={warning} alt="  " />
                      
                </Button>
                <Modal 
                    isOpen={modal2IsOpen}
                    onRequestClose={() => setModal2IsOpen(false)}
                >
                    <TitleSpan>  </TitleSpan>
                    <p>
                                
                        <br/>
                        40dB   
                        <br/>
                         
                    </p>
                    <Span>   </Span>
                    <p>,   ,   , , , ,     </p>
                    <Line></Line>
                    <br />
                    <AlarmSpan> ?</AlarmSpan>
                    <SelectButton>
                        <YesButton onClick={()=>{
                                handleSubmit(2)
                                }
                            }
                        ></YesButton>
                        <NoButton></NoButton>
                    </SelectButton>
                </Modal>
                <Button onClick={() => setModal3IsOpen(true)}>
                    <img src={safe} alt="  " />
                      
                </Button>
                <Modal
                    isOpen={modal3IsOpen}
                    onRequestClose={() => setModal3IsOpen(false)}
                >
                    <TitleSpan>  </TitleSpan>
                    <p>
                              
                        <br/>
                            
                        <br/>
                         
                    </p>
                    <Span>   </Span>
                    <p>, ,   </p>
                    <Line></Line>
                    <br />
                    <AlarmSpan> ?</AlarmSpan>
                    <SelectButton>
                        <YesButton onClick={()=>{
                                handleSubmit(1)
                                }
                            }></YesButton>
                        <NoButton></NoButton>
                    </SelectButton>
                </Modal>
            </Buttons>
        </Container>
    )
}

export default EmergencyStep;
