import { getGeoBoundary } from "../../../../APIs/MapAPIs";
import { getBoundaryPoints } from "../../../../APIs/UnitConvert";
import colors from "../../../../Common/Color";
const { kakao } = window

/**
 * 
 * @param {*} sd_code  
 * @return  path
 */
const getPolygonPath = async (sd_code) =>{
    const boundaryList = await getGeoBoundary(sd_code)
    
    const result = []
    let resultC = []
    boundaryList.forEach(singlePolygon =>{
        resultC = []
        singlePolygon.forEach(element => {
            resultC.push(new kakao.maps.LatLng(element[0], element[1]))
        });
        
        result.push(resultC)
    })
    return result
}

/**
     * 
     * @param {*} sd_code_List     
     * @return      
     */
const createPolygon = async (sd_code) =>{
    /**   */
    var polygonPath = [];
    const pathData = await getPolygonPath(sd_code)
    pathData.forEach(singlePath => {
        polygonPath.push(singlePath)
        
    });
    //    
    var polygon = new kakao.maps.Polygon({
        path:polygonPath, //    
        strokeWeight: 1.5, //  
        strokeColor: colors.red, //  
        strokeOpacity: 0.4, //    1 0   0  
        strokeStyle: 'solid', //  
        fillColor: colors.red, //  
        fillOpacity: 0.25, //   
    });
    
    
    return polygon
    
}
/**
 * @param {*} sdList 
 * @returns       
 */
const makePolygon = async(sdList) =>{
    const polygonList = []
    for (const sdcode of sdList) {
        const polygon = await createPolygon(sdcode);
        polygonList.push(polygon);
    }
    return polygonList
}

/**
 * @param {*} polygonList   
 * @param {*} map   
 * @retrunt None
 *        
 */
const drawPolygon = (polygonList, map) =>{
    console.log('in draw')
    console.log(polygonList)
    if(polygonList.length > 0){
        for (const polygon of polygonList){
            polygon.setMap(map)
        }
    }
}

/**
 * @param {*} polygonList   
 * @param {*} map   
 *        
*/
const erasePolygon = (polygonList, map) =>{
    console.log('in erase')
    console.log(polygonList)
    if(polygonList.length > 0){
        for (const polygon of polygonList){
            polygon.setMap(null)
        }
    }
}
export {getPolygonPath, makePolygon, erasePolygon, createPolygon, drawPolygon}