import axios from "axios"
import instance from "./Instance";
import Cookies from 'js-cookie'; // js-cookie  
import { getCookieToken, getaccessToken, setRefreshToken, setaccessToken } from "../Common/Cookie";
import { cookies } from "../Common/Cookie";
/**
 *   baseURL
 */
// const baseURL = '/api'

// /**
//  *   baseURL
//  */
const baseURL = 'http://127.0.0.1:8000/api'


/**
 *     acc/rfc  
 * @param {*} email 
 * @param {*} pw 
 * @returns  /    true/false 
 *  
 */

const login = async (email, pw) => {
  const apiURL = baseURL + '/user/auth/';
  const requestData = {
    'email': email,
    'password': pw
  };
  const finaldata = JSON.stringify(requestData);

  try {
    const response = await axios.post(apiURL, finaldata, {
      headers: {
        'Content-Type': 'application/json',
      },
      withCredentials: true,
    });

    console.log(response);
    const accessToken = response.data.token.access;
    const refreshToken = response.data.token.refresh;

    // js-cookie    
    Cookies.set('access_token', accessToken);
    Cookies.set('refresh_token', refreshToken);

    instance.defaults.headers.common['Authorization'] = `Bearer ${accessToken}`;
    localStorage.setItem('access_token', accessToken);
    localStorage.setItem('refresh_token', refreshToken);

    const currentTime = new Date();
    const expireTime = new Date(currentTime.getTime() + (9 * 60 + 45) * 1000);
    localStorage.setItem('expiresAt', expireTime.toISOString());
    alert(' ');
    // navigate('/')
  } catch (error) {
    console.log(error);
    alert(' ');
    throw error;
  }
};

  

/**
 * 
 * @param {*} dis_level 
 * @param {*} email 
 * @param {*} nickname 
 * @param {*} password 
 * @returns    acc 
 */
const logOut =  async() => {
    const apiURL = baseURL + '/user/auth/'
    
    await instance.delete(apiURL)
    .then(()=>{
        console.log('  ')
        // axios  access  
        instance.defaults.headers.common['Authorization'] = undefined;
        localStorage.removeItem('access_token');
        localStorage.removeItem('refresh_token');
        return 'logout '
    })
    .catch(()=>{
        console.log(' !!!')
        return 'logout !!!'
    })
    //  access  
}

const register = (email, pw) => {
    // axios  jwt   .
    const apiURL = baseURL + '/user/register/'
    const requestData = {
        'email': email,
        'password': pw,
        'nickname' : email,
        'dis_level' : 1
    }
    const finaldata = JSON.stringify(requestData)
    console.log(finaldata)
    return axios.post(apiURL, finaldata, {
        headers: {
          'Content-Type': 'application/json', // JSON    
        },
    })
    .then(() => {
        alert(' ')
        return true;
        //  httponly    
    }).catch((error) => {
        console.log(error)
        //      
        alert(' ');
        return false;
    })
}
/**
 * 
 * @returns user  API
 */
const getUserAuth = async () =>{
    const apiURL = baseURL + "/user/auth/"
    
    return await instance.get(apiURL, {withCredentials:true})
    .then((response) =>{
        return(response)
    }).catch((error) => {
        console.log(error)
        console.log('Get User Auch !!')
    })
}
const updateUserAuth = async (auth) =>{
    const apiURL = baseURL + "/user/auth/update/"

    const requestData = {
        'email': auth.email,
        'dis_level': auth.dis_level,
        'password': auth.password,
        'nickname' : auth.nickname
    }

    const finaldata = JSON.stringify(requestData)
    console.log(finaldata)
    return await instance.put(
        apiURL,
        finaldata,
        {
            withCredentials: true,
        }
    ).then((response) =>{
            return(response)}
    ).catch((error) => {
        console.log(error)  
    }
    )
}
/**
 * refresh   access token   
 * @param {*} refreshToken 
 */
const refresh = async (refreshToken) => {
    const apiURL = baseURL + "/user/auth/refresh/"
    const requestData = {
        'refresh' : localStorage.getItem('refresh_token'),
    }
    const finaldata = JSON.stringify(requestData)
    instance.post(
        apiURL,
        finaldata,
        {
            withCredentials: true,
        }
    )
    .then((response)=>{
        console.log('refresh data');
        console.log(response.data);
    })
    .catch((error)=>{
        console.log(error);
        console.log('refresh  ')
    })
}

